<template>
  <div class="container">
    <div class="container__protrait">
      <img
        src="https://markdown-1253389072.cos.ap-nanjing.myqcloud.com/202202221429453.png"
        alt=""
        class="container__protrait__img"
      />
    </div>
    <div class="container__input">
      <input
        type="text"
        class="container__input__username"
        placeholder="请输入手机号"
        v-model="phone"
      />
      <input
        type="passowrd"
        class="container__input__password"
        placeholder="请输入密码"
        v-model="password"
      />
      <input
        type="passowrd"
        class="container__input__password"
        placeholder="确认密码"
        v-model="confirmPassword"
      />
    </div>
    <div class="container__button">
      <button class="container__button__register" @click="handleRegister">
        注册
      </button>
    </div>
    <div class="container__content">
      <a href="javascript:;" class="container__content__a" @click="handleLogin"
        >已有账号前去登录</a
      >
    </div>
  </div>
  <toast v-if="isShow" :message="toastMessage"></toast>
</template>
<script>
import { reactive, toRefs } from 'vue'
import { post } from '../../utils/request'
import { useRouter } from 'vue-router'
import Toast, { showToastEffect } from '../../components/Toast.vue'
// 定义用户注册的逻辑接口
const userRegisterEffect = (showToast) => {
  const data = reactive({
    phone: '',
    password: '',
    confirmPassword: ''
  })
  const router = useRouter()
  const handleRegister = async () => {
    if (data.phone === '') {
      showToast('用户名不能为空！')
      return
    }
    if (data.password === '') {
      showToast('密码不能为空！')
      return
    }
    if (data.confirmPassword === '') {
      showToast('确认密码不能为空！')
      return
    }
    if (data.password !== data.confirmPassword) {
      showToast('密码不一致！')
    } else {
      try {
        const result = post('/api/user/register', data)
        if (result.data.code === '0000') {
          localStorage.setItem('isLogin', 'true')
          router.push({ name: 'Home' })
        } else {
          showToast('注册失败!')
        }
      } catch (error) {
        showToast('发送失败！')
      }
    }
  }
  const { phone, password, confirmPassword } = toRefs(data)
  return { phone, password, confirmPassword, handleRegister }
}
// 定义跳转登录的逻辑
const userLoginEffect = () => {
  const router = useRouter()
  const handleLogin = () => {
    router.push({ name: 'Login' })
  }
  return { handleLogin }
}
// 默认导出
export default {
  name: 'Register',
  components: { Toast },
  setup () {
    const { isShow, showToast, toastMessage } = showToastEffect()
    const { phone, password, confirmPassword, handleRegister } =
      userRegisterEffect(showToast)
    const { handleLogin } = userLoginEffect()
    return {
      handleLogin,
      isShow,
      phone,
      password,
      confirmPassword,
      handleRegister,
      toastMessage
    }
  }
}
</script>
<style lang="scss" scoped>
.container {
  &__protrait {
    margin-top: 120rem;
    margin-bottom: 40rem;
    text-align: center;
    &__img {
      width: 66rem;
      height: 66rem;
    }
  }
  &__input {
    margin-bottom: 32rem;
    text-align: center;
    &__username,
    &__password {
      width: 279rem;
      height: 48rem;
      background: #f9f9f9;
      border: 1px solid rgba(0, 0, 0, 0.1);
      border-radius: 6rem;
      margin-bottom: 16rem;
      font-family: PingFangSC-Regular;
      font-size: 16rem;
      color: rgba(0, 0, 0, 0.5);
      letter-spacing: 0;
      line-height: 24rem;
      padding-left: 16rem;
    }
  }
  &__button {
    text-align: center;
    margin-bottom: 16rem;
    &__register {
      width: 295rem;
      height: 48rem;
      background: #0091ff;
      box-shadow: 0 4rem 8rem 0 rgba(0, 145, 255, 0.32);
      border-radius: 4rem;
      font-family: PingFangSC-Regular;
      font-size: 16px;
      color: #ffffff;
      letter-spacing: 0;
      line-height: 24rem;
    }
  }
  &__content {
    display: flex;
    margin: 0 auto;
    width: 98rem;
    height: 20rem;
    justify-content: space-between;
    align-items: center;
    &__a,
    &__gap {
      font-family: PingFangSC-Regular;
      font-size: 14rem;
      color: rgba(0, 0, 0, 0.5);
      letter-spacing: 0;
      text-decoration: none;
      -webkit-tap-highlight-color: transparent;
    }
  }
}
</style>
